import axios from "axios";
import {
  AGENT_REGISTER_FAIL,
  AGENT_REGISTER_REQUEST,
  AGENT_REGISTER_SUCCESS,
  AGENT_SIGNIN_FAIL,
  AGENT_SIGNIN_REQUEST,
  AGENT_SIGNIN_SUCCESS,
} from "../constansts/agentConstants";

export const agentRegister = (dataObj) => async (dispatch) => {
  dispatch({ type: AGENT_REGISTER_REQUEST, payload: dataObj });
  try {
    let res = await axios.post(`http://localhost:4242/agent/signup`, dataObj);
    if (res.data.status) {
      dispatch({ type: AGENT_REGISTER_SUCCESS, payload: res.data });
    } else if (res.data.error) {
      dispatch({ type: AGENT_REGISTER_FAIL, payload: res.data.error });
    } else {
      throw new Error(res.data.message);
    }
  } catch (error) {
    dispatch({ type: AGENT_REGISTER_FAIL, payload: error });
  }
};

export const agentLogin = (dataObj) => async (dispatch) => {
  dispatch({ type: AGENT_SIGNIN_REQUEST, payload: dataObj });
  try {
    let res = await axios.post(`http://localhost:4242/agent/login`, dataObj);
    if (res.data.status) {
      dispatch({ type: AGENT_SIGNIN_SUCCESS, payload: res.data });
    } else {
      throw new Error(res.data.message);
    }
  } catch (error) {
    dispatch({ type: AGENT_SIGNIN_FAIL, error: error });
  }
};
