export default function SelectCourse({ streams }) {
  streams = streams.map((stream) => {
    return { label: stream.programs_stream, value: stream.programs_stream };
  });
  return (
    <div>
      <Field
        id={"course"}
        name={"course"}
        label={"What disciplines do you want to study? "}
        component={FormMultiSelect}
        textField={"label"}
        data={streams}
        //   validator={requiredValidator}
      />
    </div>
  );
}
