import React, { useState } from "react";

import { Form, FormElement } from "@progress/kendo-react-form";
import { Button } from "@progress/kendo-react-buttons";
import { Stepper } from "@progress/kendo-react-layout";
import { RecentGradesForm } from "./recentGradesForm";
import ExamType from "./ExamType";
import SelectCourse from "./SelectCourse";

export default function Main({ oneFunc, countries, highEducation, streams }) {
  const stepPages = [
    <RecentGradesForm countries={countries} highEducation={highEducation} />,
    <ExamType />,
    <SelectCourse streams={streams} />,
  ];
  const [step, setStep] = useState(0);
  const [formState, setFormState] = React.useState({});
  const [steps, setSteps] = React.useState([
    { label: "Recent Grades", isValid: undefined },
    { label: "English Exam Type", isValid: undefined },
    { label: "Choose Disciplines", isValid: undefined },
  ]);

  const lastStepIndex = steps.length - 1;
  const isLastStep = lastStepIndex === step;
  const onStepSubmit = React.useCallback(
    (event) => {
      const { isValid, values } = event;

      const currentSteps = steps.map((currentStep, index) => ({
        ...currentStep,
        isValid: index === step ? isValid : currentStep.isValid,
      }));

      setSteps(currentSteps);

      if (!isValid) {
        return;
      }

      setStep(() => Math.min(step + 1, lastStepIndex));
      setFormState(values);

      if (isLastStep) {
        oneFunc(values);
      }
    },
    [step, steps, setSteps, setStep, setFormState, isLastStep]
  );

  const onPrevClick = React.useCallback(
    (event) => {
      event.preventDefault();
      setStep(() => Math.max(step - 1, 0));
    },
    [step, setStep]
  );
  return (
    <div class="row">
      <div class="col-md-8">
        <div id="accordion">
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
            }}
          >
            <Stepper value={step} items={steps} />
            <Form
              initialValues={formState}
              onSubmitClick={onStepSubmit}
              render={(formRenderProps) => (
                <div style={{ alignSelf: "center" }}>
                  <FormElement style={{ width: 480 }}>
                    {stepPages[step]}
                    <span
                      style={{ marginTop: "40px" }}
                      className={"k-form-separator"}
                    />
                    <div
                      style={{
                        justifyContent: "space-between",
                        alignContent: "center",
                      }}
                      className={"k-form-buttons k-buttons-end"}
                    >
                      <span style={{ alignSelf: "center" }}>
                        Step {step + 1} of 3
                      </span>
                      <div>
                        {step !== 0 ? (
                          <Button
                            style={{ marginRight: "16px" }}
                            onClick={onPrevClick}
                          >
                            Previous
                          </Button>
                        ) : undefined}
                        <Button
                          primary={true}
                          disabled={!formRenderProps.allowSubmit}
                          onClick={formRenderProps.onSubmit}
                        >
                          {isLastStep ? "Show All Eligible Programs" : "Next"}
                        </Button>
                      </div>
                    </div>
                  </FormElement>
                </div>
              )}
            />
          </div>
        </div>
      </div>
      <div class="col-md-4">
        <div class="container">
          <div class="row">
            <div class="points__">
              <ul>
                <h4>Top features</h4>
                <li>
                  <i class="fas fa-check"></i>
                  <span>
                    We provide you the best platform to study abroad and make
                    your future bright.
                  </span>
                </li>
                <li>
                  {" "}
                  <i class="fas fa-check"></i>{" "}
                  <span>
                    We offer exact information for students interested in
                    school/college and courses.{" "}
                  </span>{" "}
                </li>
                <li>
                  <i class="fas fa-check"></i>{" "}
                  <span>
                    We help you classify the best universities that match your
                    profile.
                  </span>
                </li>
                <li>
                  <i class="fas fa-check"></i>{" "}
                  <span>
                    Our team of experts connects with students universal,
                    supporting them with our platform and services.
                  </span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
