import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

export const Navbar = () => {
  const [country, setCountry] = useState([]);
  useEffect(() => {
    axios
      .get(`${window.APIPATH}/v1/country`)
      .then((result) => {
        if (result.data.status) {
          setCountry(result.data.data);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  // const BASE_URL = "/";
  return (
    <div>
      <nav className="navbar navbar-expand-lg fixedme header">
        <div className="container">
          <Link className="navbar-brand" to="/">
            <img
              src="https://www.learnglobal.com/schoollogo/logo.png"
              className="simplelogo"
            />
            <img
              className="fixedlogo"
              style={{ display: "none" }}
              src="https://www.learnglobal.com/schoollogo/logo-fixed.png"
            />
          </Link>

          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ml-auto">
              <li className="nav-item active">
                <Link className="nav-link" to="/">
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/discover">
                  Discover School
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/Admin/selectrole">
                  Login/Signup
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/contact">
                  Contact
                </Link>
              </li>
              <li
                className="dropdown nav-item countrydropdown"
                id="country_dropdown"
              >
                <Link
                  className="dropdown-toggle nav-link"
                  data-toggle="dropdown"
                  to="#"
                  aria-expanded="true"
                >
                  Country
                </Link>
                <ul className="dropdown-menu">
                  {country.map((item) => (
                    <li key={item.id}>
                      <Link to={`/country/${item.id}`}>
                        <img
                          src={`https://www.learnglobal.com/uploads/${item.country_image}`}
                        />
                        {item.country_name}
                      </Link>
                    </li>
                  ))}
                </ul>
              </li>

              <li className="nav-item">
                <Link className="nav-link btn btn-danger" to="/eligibility">
                  Apply Now
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
};
