import {
  STUDENT_REGISTER_REQUEST,
  STUDENT_REGISTER_SUCCESS,
  STUDENT_REGISTER_FAIL,
  STUDENT_SIGNIN_REQUEST,
  STUDENT_SIGNIN_SUCCESS,
  STUDENT_SIGNIN_FAIL,
} from "../constansts/studentConstants";

//student register reducers
const studentRegisterReducer = (state = {}, action) => {
  switch (action.type) {
    case STUDENT_REGISTER_REQUEST:
      return { loading: true };
    case STUDENT_REGISTER_SUCCESS:
      return { loading: false, studentInfo: action.payload };
    case STUDENT_REGISTER_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

//student login reducer

const studentLoginReducer = (state = {}, action) => {
  switch (action.type) {
    case STUDENT_SIGNIN_REQUEST:
      return { loading: true };
    case STUDENT_SIGNIN_SUCCESS:
      return { loading: false, studentInfo: action.payload };
    case STUDENT_SIGNIN_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export { studentRegisterReducer, studentLoginReducer };
