import React, { useEffect, useState } from "react";
import Footer from "../../components/Footer";
import { Navbar } from "../../components/Navbar";
import SideBarForm from "../../components/SideBarForm";
import axios from "axios";
import SchoolCard from "../../components/SchoolCard";

export default function DiscoverSchool() {
  const [schools, setschools] = useState([]);
  const [searchText, setSearchText] = useState("");
  const [searchedSchools, setsearchedSchools] = useState([]);
  const [showSearchedResult, setshowSearchedResult] = useState(false);
  const [showAllResult, setshowAllResult] = useState(false);
  const [showButton, setshowButton] = useState(false);
  const [isLoading, setisLoading] = useState(true);
  const [errors, seterrors] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    let newArr = schools.filter(
      (item) =>
        item.country.toLowerCase().includes(searchText.toLowerCase()) ||
        item.school_name.toLowerCase().includes(searchText.toLowerCase())
    );
    setsearchedSchools(newArr);
    if (newArr.length > 0) {
      setsearchedSchools(newArr);
      setshowSearchedResult(true);
      setshowAllResult(false);
      setshowButton(true);
      seterrors(false);
    } else {
      setshowAllResult(false);
      setshowSearchedResult(false);
      setshowButton(true);
      seterrors(true);
    }
  };

  const allSchools = () => {
    setshowSearchedResult(false);
    setshowAllResult(true);
    setshowButton(false);
    seterrors(false);
  };

  useEffect(() => {
    axios
      .get(`/v1/discoverSchool`)
      .then((res) => {
        let { status, data } = res.data;
        setisLoading(false);
        setshowAllResult(true);
        setschools(data);
        seterrors(false);
      })
      .catch((error) => console.log(error));
  }, []);

  return (
    <>
      <Navbar />
      <section id="discoverSchool" className="eligibleSteps discoverSchool">
        <div className="container text-center">
          <div className="row">
            <div className="col-md-8">
              <div className="row searchBar">
                <div className="col-md-12 mb-2">
                  <h3 className="mainheading text-left">
                    Search Your Favorite School, University with Country
                  </h3>
                </div>
                <div className="col-md-12">
                  <form onSubmit={handleSubmit} className="mb-3">
                    <div className="input-group">
                      <input
                        type="text"
                        className="form-control search_box"
                        placeholder="Find School, University or Country"
                        onChange={(e) => {
                          setSearchText(e.target.value);
                        }}
                      />
                      <div className="input-group-append">
                        <button
                          className="btn btn-outline-secondary searchBtn"
                          type="submit"
                        >
                          Search
                        </button>
                      </div>
                    </div>
                  </form>
                  {showButton && (
                    <button
                      className="btn btn-primary btn-md"
                      onClick={() => allSchools()}
                    >
                      All Schools
                    </button>
                  )}
                </div>
              </div>
              <div className="row school_gets">
                {isLoading && <h3>Loading...</h3>}
                {errors && <h5>No result found..</h5>}
                {schools &&
                  showAllResult &&
                  schools.map((item, index) => (
                    <div className="col col1 mb-3" key={index}>
                      <SchoolCard item={item} />
                    </div>
                  ))}
                {searchedSchools && showSearchedResult
                  ? searchedSchools.map((item, index) => (
                      <div className="col col1 mb-3" key={index}>
                        <SchoolCard item={item} />
                      </div>
                    ))
                  : null}
              </div>
            </div>

            <div className="col-md-4">
              <SideBarForm />
            </div>
          </div>
        </div>
      </section>

      <Footer />
    </>
  );
}
