import React, { useEffect, useState } from "react";
import Footer from "../../components/Footer";
import { Navbar } from "../../components/Navbar";
import SideBarForm from "../../components/SideBarForm";
import axios from "axios";
import { Link } from "react-router-dom";

export default function SchoolInfo(props) {
  const [schoolInfo, setSchoolInfo] = useState({});
  const [error, seterror] = useState("");
  let { id } = props.match.params;

  useEffect(() => {
    axios
      .get(`${window.APIPATH}/v1/school_info/${id}`)
      .then((res) => {
        let { status, data, message } = res.data;
        if (status) {
          setSchoolInfo(data);
        } else {
          setSchoolInfo(false);
          seterror(message);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <div>
      <Navbar />
      {/* <!--Inner Page Banner section--> */}
      {error.length > 0 ? <p className="text-danger">{error}</p> : null}
      {schoolInfo ? (
        <div>
          <div className="innerpageBanner singleSchoolBanner">
            <div className="container">
              {schoolInfo?.school_logo?.logo && (
                <img
                  className="univimg"
                  src={`https://www.learnglobal.com/schoollogo/${schoolInfo?.school_logo?.logo}`}
                />
              )}
              <h4 className="mainheading">{schoolInfo?.school_name}</h4>
              <div className="unidetails">
                <small className="universityINFO">
                  <img
                    className="countryFlag"
                    src={`https://www.learnglobal.com/schoollogo/${schoolInfo?.country_logo}`}
                  />

                  {schoolInfo?.country}
                </small>
                {/*<small class="uniID">School ID: <span class="uniIDvalue">168</span></small>*/}{" "}
              </div>
              <div className="row unihighlights">
                <div className="col-md-3">
                  <div className="innercol">
                    <i className="fab fa-fort-awesome" />
                    <div className="uniiconsDetails">
                      <strong>Founded</strong>
                      <p>{schoolInfo?.school_logo?.founded}</p>
                    </div>
                  </div>
                </div>
                <div className="col-md-3">
                  <div className="innercol">
                    <i className="fas fa-chalkboard-teacher" />
                    <div className="uniiconsDetails">
                      <strong>Type</strong>
                      <p>Private College </p>
                    </div>
                  </div>
                </div>
                <div className="col-md-3">
                  <div className="innercol">
                    <i className="fas fa-users" />
                    <div className="uniiconsDetails">
                      <strong>Total Students</strong>
                      <p>{schoolInfo?.school_about_info?.total_student}</p>
                    </div>
                  </div>
                </div>
                <div className="col-md-3">
                  <div className="innercol">
                    <i className="fas fa-user-friends" />
                    <div className="uniiconsDetails">
                      <strong> Int. Students</strong>
                      <p>{schoolInfo?.school_about_info?.int_student}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/*Inner Page Banner section closed*/}
          <div className="singleSchoolPage padding70">
            <div className="container">
              <div className="row singlePageTabs">
                <div className="col-md-2">
                  <ul className="nav nav-tabs" role="tablist">
                    <li className="nav-item">
                      <a
                        className="nav-link active"
                        href="#singleAbout"
                        role="tab"
                        data-toggle="tab"
                      >
                        About
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        className="nav-link"
                        href="#singleFeatures"
                        role="tab"
                        data-toggle="tab"
                      >
                        Features
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        className="nav-link"
                        href="#singleLocation"
                        role="tab"
                        data-toggle="tab"
                      >
                        Location
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        className="nav-link"
                        href="#singlePrograms"
                        role="tab"
                        data-toggle="tab"
                      >
                        Programs
                      </a>
                    </li>
                  </ul>
                </div>
                <div className="col-md-6">
                  <div className="tab-content">
                    <div
                      role="tabpanel"
                      className="tab-pane fade in active show"
                      id="singleAbout"
                    >
                      <h3 className="singlePageHeading">About Us</h3>
                      <p>{schoolInfo?.school_about_info?.about}</p>
                    </div>
                    <div
                      role="tabpanel"
                      className="tab-pane fade"
                      id="singleFeatures"
                    >
                      <div className="row sfp1">
                        <div
                          className={`col-md-6 mb-4 ${
                            schoolInfo?.school_features?.work_permit === "yes"
                              ? ""
                              : "disable_feature"
                          }`}
                        >
                          <i className="fas fa-university" />
                          <p>Post-Grad Work Permit</p>
                        </div>
                        <div
                          className={`col-md-6 mb-4 ${
                            schoolInfo?.school_features?.program_cooporation ===
                            "yes"
                              ? ""
                              : "disable_feature"
                          }`}
                        >
                          <i className="fas fa-briefcase" />
                          <p>Programs with Co-op Option</p>
                        </div>
                        <div
                          className={`col-md-6 mb-4 ${
                            schoolInfo?.school_features?.work_while_study ===
                            "yes"
                              ? ""
                              : "disable_feature"
                          }`}
                        >
                          <i className="fas fa-hand-holding-usd" />
                          <p>Work While Studying</p>
                        </div>
                        <div
                          className={`col-md-6 mb-4 ${
                            schoolInfo?.school_features
                              ?.condition_offer_letter === "yes"
                              ? ""
                              : "disable_feature"
                          }`}
                        >
                          <i className="far fa-file-alt" />
                          <p>Conditional Offer Letter</p>
                        </div>
                        <div
                          className={`col-md-6 mb-4 ${
                            schoolInfo?.school_features?.accomodation === "yes"
                              ? ""
                              : "disable_feature"
                          }`}
                        >
                          {/*<i class="fa fa-ban"></i>*/}
                          <i className="fas fa-hotel" />
                          <p>Type of Accommodation</p>
                        </div>
                        <div
                          className={`col-md-6 mb-4 ${
                            schoolInfo?.school_features?.library === "yes"
                              ? ""
                              : "disable_feature"
                          }`}
                        >
                          <i className="fas fa-book-open" />
                          <p>Library</p>
                        </div>
                      </div>
                    </div>
                    <div
                      role="tabpanel"
                      className="tab-pane fade"
                      id="singleLocation"
                    >
                      {schoolInfo?.school_location}
                      <div style={{ width: 700, height: 400 }} id="map" />
                    </div>
                    <div
                      role="tabpanel"
                      className="tab-pane fade"
                      id="singlePrograms"
                    >
                      <div className="row get_schools">
                        {schoolInfo?.programs_course &&
                          schoolInfo?.programs_course.map((item) => (
                            <div className="col-md-6">
                              <div className="innercourseDATA">
                                <div className="courseImgwrapper">
                                  <a
                                    href={`/singleCourse/${item.id}`}
                                    className="innerwrapper"
                                  >
                                    <img
                                      src="https://www.learnglobal.com/schoollogo/abit.png"
                                      className="img-fluid"
                                    />
                                  </a>
                                </div>
                                <div className="txtcontent row">
                                  <a
                                    href={`/singleCourse/${item.id}`}
                                    className="univName course-name_"
                                  >
                                    {item.course}
                                  </a>
                                  <small className="universityINFO">
                                    <img
                                      className="countryFlag"
                                      src={`https://www.learnglobal.com/schoollogo/${schoolInfo?.country_logo}`}
                                    />{" "}
                                    {schoolInfo?.school_location}
                                  </small>
                                </div>
                                <div className="row coursepopulardata">
                                  <div className="col-md-6">
                                    <p>
                                      {
                                        schoolInfo?.school_about_info
                                          ?.total_student
                                      }
                                    </p>
                                    <p>Student's</p>
                                  </div>
                                  <div className="col-md-6">
                                    <p className="colg-name">
                                      Private College{" "}
                                    </p>
                                  </div>
                                </div>
                                <div className="row coursepopulardata">
                                  <div className="col-md-6">
                                    <p>Private College </p>
                                    <p />
                                  </div>
                                  <div className="col-md-6">
                                    <p className="colg-name">
                                      {schoolInfo?.school_logo?.founded}
                                    </p>
                                    <p>Founded</p>
                                  </div>
                                </div>
                                <Link to="/eligibility">
                                  <button
                                    type="button"
                                    className="btn btn-secondary applybtn"
                                  >
                                    Check Eligibility
                                  </button>
                                </Link>
                              </div>
                            </div>
                          ))}
                      </div>
                      {/* <div className="col-md-12">
                        <div className="txtholder">
                          <a
                            href="javascript:void(0);"
                            data-id={168}
                            data-stype="Private College"
                            className="view_more"
                          >
                            View More
                            <i className="fas fa-arrow-right" />
                          </a>
                        </div>
                      </div> */}
                    </div>
                  </div>
                </div>
                <div className="col-md-4">
                  <SideBarForm />
                </div>
              </div>
              <div className="col-md-3"></div>
            </div>
          </div>
        </div>
      ) : (
        <div>
          <p>School not found...</p>
        </div>
      )}
      <Footer />
    </div>
  );
}
