import React, { useState } from "react";
import { Field } from "@progress/kendo-react-form";

import { FormComboBox, FormNumericTextBox } from "./form-componentss.jsx";

import {
  requiredValidator,
  readingValidator,
  writingValidator,
  listeningValidator,
  speakingValidator,
} from "./validators.jsx";

const genders = [
  { label: "I don't have this", value: "no" },
  { label: "IELTS", value: "ielts" },
  { label: "CELPIP", value: "ielts" },
  { label: "CB IELTS", value: "ielts" },
  { label: "TOFEL", value: "tofel" },
  { label: "PTE", value: "pte" },
];

export default function ExamType() {
  const [hide, sethide] = useState(false);
  const handleChange = (e) => {
    if (e.value.value == "ielts") {
      sethide(true);
    } else {
      sethide(false);
    }
  };

  return (
    <div className="row">
      <div className="col-md-12">
        <Field
          id={"selectExam"}
          name={"selectExam"}
          label={"Select Exam"}
          component={FormComboBox}
          onChange={handleChange}
          textField={"label"}
          data={genders}
          validator={requiredValidator}
        />
      </div>
      {hide ? (
        <div className="row">
          <div className="col-sm-3">
            <Field
              id={"reading"}
              name={"reading"}
              label={"Reading"}
              component={FormNumericTextBox}
              textField={"label"}
              validator={readingValidator}
            />
          </div>

          <div className="col-sm-3">
            <Field
              id={"writing"}
              name={"writing"}
              label={"Writing"}
              component={FormNumericTextBox}
              textField={"label"}
              validator={writingValidator}
            />
          </div>

          <div className="col-sm-3">
            <Field
              id={"listening"}
              name={"listening"}
              label={"Listening"}
              component={FormNumericTextBox}
              textField={"label"}
              validator={listeningValidator}
            />
          </div>

          <div className="col-sm-3">
            <Field
              id={"speaking"}
              name={"speaking"}
              label={"Speaking"}
              component={FormNumericTextBox}
              textField={"label"}
              validator={speakingValidator}
            />
          </div>
        </div>
      ) : null}
    </div>
  );
}
