import React, { Suspense } from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import LoadingComponent from "../components/loading";
import About from "../pages/About";
import AgentLogin from "../pages/AgentLogin";
import AgentPage from "../pages/AgentPage";
import AgentSignup from "../pages/AgentSignup";
import Contact from "../pages/Contact";
import Country from "../pages/Country";
import DiscoverSchool from "../pages/DIscoverSchools";
import Eligibility from "../pages/Eligibility";
import FAQ from "../pages/FAQ";
import Homepage from "../pages/Homepage";
import PrivacyPolicy from "../pages/PrivacyPolicy";
import SchoolInfo from "../pages/SchoolInfo";
import SelectRole from "../pages/SelectRole";
import SingleCourse from "../pages/SingleCourse";
import StudentPage from "../pages/StudentPage";
import Terms from "../pages/Terms";
// import SLUGS from 'resources/slugs';

function PublicRoutes() {
  return (
    <Suspense fallback={<LoadingComponent loading />}>
      <Switch>
        <Route exact path="/" component={Homepage} />
        <Route path="/discover" component={DiscoverSchool} />
        <Route path="/contact" component={Contact} />
        <Route path="/country/:id" component={Country} />
        <Route path="/eligibility" component={Eligibility} />
        <Route path="/Admin/selectrole" component={SelectRole} />
        <Route path="/studentPage" component={StudentPage} />
        <Route path="/agentPage" component={AgentPage} />
        <Route path="/agentSignup" component={AgentSignup} />
        <Route path="/agentLogin" component={AgentLogin} />
        <Route path="/singleCourse/:id" component={SingleCourse} />
        <Route path="/schoolInfo/:id" component={SchoolInfo} />
        <Route path="/about" component={About} />
        <Route path="/faq" component={FAQ} />
        <Route path="/terms" component={Terms} />
        <Route path="/privacy" component={PrivacyPolicy} />
      </Switch>
    </Suspense>
  );
}

export default PublicRoutes;
