import React, { useEffect } from "react";
import { useHistory } from "react-router";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { useDispatch, useSelector } from "react-redux";
import { registerStudent } from "../../store/actions/studentAction";
import swal from "sweetalert";

//validation schema
const schema = yup.object().shape({
  first_name: yup.string().required("First name is required."),
  last_name: yup.string().required("Last name is required."),
  email: yup
    .string()
    .email("Please enter a valid email.")
    .required("Email is required."),
  phone_number: yup
    .number()
    .typeError("Mobile number should be in digits.")
    .required("Mobile number is required."),
  password: yup.string().required("Please enter your password."),
  confirmpassword: yup
    .string()
    .oneOf([yup.ref("password"), null], "Password not matched")
    .required("Please confirm your password."),
});

export default function StudentRegister(props) {
  const history = useHistory();
  const { register, handleSubmit, errors } = useForm({
    resolver: yupResolver(schema),
  }); // initialize the hook
  const studentRegister = useSelector((state) => state.studentRegister);
  const { loading, studentInfo, error } = studentRegister;
  const dispatch = useDispatch();
  const onSubmit = (data) => {
    dispatch(registerStudent(data));
    //handle submit here for something
  };
  useEffect(() => {
    if (studentInfo === undefined) {
      return;
    } else {
      if (studentInfo.status) {
        swal({
          title: "Good job!",
          text: studentInfo.message,
          icon: "success",
          button: "Okay",
        });
        history.push("/studentLogin");
      } else {
        error = studentInfo.message;
      }
    }
    return () => {};
  }, [studentInfo]);

  return (
    <form
      onSubmit={handleSubmit(onSubmit)}
      id="register-form"
      className="email-signup hideFORM"
    >
      {loading && <div>Loading...</div>}
      {error && <div>{error}</div>}
      <div className="u-form-group">
        <input
          type="text"
          name="first_name"
          className="myinput"
          id="firstname"
          placeholder="Firstname*"
          ref={register}
        />
        <div>{errors.first_name?.message}</div>
      </div>
      <div className="u-form-group">
        <input
          type="text"
          name="last_name"
          className="myinput"
          id="lastname"
          placeholder="Lastname*"
          ref={register}
        />
        <div>{errors.last_name?.message} </div>
      </div>
      <div className="u-form-group">
        <input
          type="text"
          name="phone_number"
          className="myinput"
          id="phone"
          placeholder="Phone Number*"
          ref={register}
        />
      </div>
      <div>{errors.phone_number?.message} </div>
      <div className="u-form-group">
        <input
          type="email"
          name="email"
          className="myinput"
          id="email"
          placeholder="Email*"
          ref={register}
        />
      </div>
      <div>{errors.email?.message} </div>
      <div className="u-form-group">
        <input
          type="password"
          name="password"
          className="myinput"
          id="password"
          placeholder="Password*"
          ref={register}
        />
      </div>
      <div>{errors.password?.message} </div>
      <div className="u-form-group">
        <input
          type="password"
          name="confirmpassword"
          className="myinput"
          id="confpassword"
          placeholder="Confirm Password*"
          ref={register}
        />
      </div>
      <div>{errors.confirmpassword?.message} </div>
      <div className="u-form-group">
        <div
          data-sitekey="6LdpevAUAAAAAGoPY3VKTZ1U-mm3_sc-6UPJgky_"
          data-theme="light"
          className="g-recaptcha"
        />
      </div>
      <div className="u-form-group"></div>
      <div className="u-form-group signupBTN">
        <button type="submit" name="submit" className="sbmitbtnLoginstudent">
          Sign Up
        </button>
      </div>
    </form>
  );
}
