import axios from "axios";
import {
  STUDENT_REGISTER_FAIL,
  STUDENT_REGISTER_REQUEST,
  STUDENT_REGISTER_SUCCESS,
  STUDENT_SIGNIN_FAIL,
  STUDENT_SIGNIN_REQUEST,
  STUDENT_SIGNIN_SUCCESS,
} from "../constansts/studentConstants";
//student register action
export const registerStudent = (dataObj) => async (dispatch) => {
  dispatch({ type: STUDENT_REGISTER_REQUEST, payload: dataObj });
  try {
    let res = await axios.post(`http://localhost:4242/signup`, dataObj);
    console.log(res.data);
    if (res.data.status) {
      dispatch({ type: STUDENT_REGISTER_SUCCESS, payload: res.data });
    } else if (res.data.error) {
      dispatch({ type: STUDENT_REGISTER_FAIL, payload: res.data.error });
    } else {
      throw new Error(res.data.message);
    }
  } catch (error) {
    console.log(error);
    dispatch({ type: STUDENT_REGISTER_FAIL, payload: error.message });
  }
};

//student login action
export const studentLogin = (dataObj) => async (dispatch) => {
  dispatch({ type: STUDENT_SIGNIN_REQUEST, payload: dataObj });
  try {
    let res = await axios.post(`http://localhost:4242/login`, dataObj);
    if (res.data.status) {
      dispatch({ type: STUDENT_SIGNIN_SUCCESS, payload: res.data });
    } else if (res.data.error) {
      dispatch({ type: STUDENT_SIGNIN_FAIL, payload: res.data.error });
    } else {
      throw new Error(res.data.message);
    }
  } catch (error) {
    dispatch({ type: STUDENT_SIGNIN_FAIL, payload: error.message });
  }
};
