import * as React from "react";
import { getter } from "@progress/kendo-react-common";

const emailRegex = new RegExp(/\S+@\S+\.\S+/);
const phoneRegex = new RegExp(/^[0-9 ()+-]+$/);
const ccardRegex = new RegExp(/^[0-9-]+$/);
const cvcRegex = new RegExp(/^[0-9]+$/);

//nationality validator
export const nationalityValidator = (value) =>
  !value ? "Select your country." : "";

//highEducation validator
export const highEducationValidator = (value) =>
  !value ? "Select your highest education." : "";

//grading Schema validator
export const gradingSchemaValidator = (value) =>
  !value ? "Select your grading schema." : "";

//average grade validator
export const gradeAverageValidator = (value) =>
  !value ? "Please provide your average grade." : "";

//email validator
export const emailValidator = (value) =>
  !value
    ? "Email field is required."
    : emailRegex.test(value)
    ? ""
    : "Email is not valid format.";

//name validator

export const nameValidator = (value) =>
  !value
    ? "Full Name is required"
    : value.length < 5
    ? "Full Name should be at least 7 characters long."
    : "";
