import React, { useState, useEffect } from "react";
import axios from "axios";

import "./eligibilityStyle.css";
import Footer from "../../components/Footer";
import { Navbar } from "../../components/Navbar";

import Main from "../../components/multiStep/main";

import { Modal, Button, Form } from "react-bootstrap";

const highEducation = [
  { label: "Grade 12/High School", programLevel: "1", value: "secondary" },
  {
    label: "1-Year Post-Secondary Certificate",
    programLevel: "1",
    value: "certificate",
  },
  {
    label: "2-Year Undergraduate Diploma",
    programLevel: "1",
    value: "diploma",
  },
  {
    label: "3-Year Undergraduate Advanced Diploma",
    programLevel: "1",
    value: "advance_diploma",
  },
  {
    label: "3-Year Bachelor's Degree",
    programLevel: "2",
    value: "3_year_bachlor",
  },
  {
    label: "4-Year Bachelor's Degree",
    programLevel: "2",
    value: "4_year_bachlor",
  },
  {
    label: "Postgraduate Certificate/Diploma",
    programLevel: "2",
    value: "postgraduate_diploma",
  },
  { label: "Master's Degree", programLevel: "2", value: "master" },
  {
    label: "Doctoral Degree (Phd, M.D., ...)",
    programLevel: "2",
    value: "doctrate",
  },
];

export default function Eligibility() {
  const [heading, setheading] = useState(false);
  const [stream, setstream] = useState([]);
  const [resultedData, setResultedData] = useState([]);
  const [resultedPrograms, setResultedPrograms] = useState([]);

  //open model
  const [show, setShow] = useState(false);

  //handle show model
  const handleShow = (id) => {
    console.log(id);
    axios
      .get(`${window.APIPATH}/v1/checkProgram?school=${id}`)
      .then((result) => {
        setResultedPrograms(result.data.result);
        setShow(true);
      })
      .catch((error) => console.log(error));
  };

  // handle hide model
  const handleClose = () => setShow(false);

  //get data from eligibile form
  const oneFunc = (values) => {
    console.log(values);

    let { course } = values;

    let course_list = course === undefined ? "All" : course.map((c) => c.value);
    let queryURL = `${window.APIPATH}/v1/checkEligibility?nationality=${
      values.nationality.value
    }&highEducation=${values.highestEducation.value}&program_level=${
      values.highestEducation.programLevel
    }&grade_scheme=${values.gradingSchema.value}&grade_average=${
      values.gradeAverage
    }&name=${values.name}&email=${values.email}&phone=${
      values.phone
    }&country_education=${
      values?.country_education !== undefined
        ? values.country_education?.value
        : "All"
    }&exam_type=${values.selectExam.value}&stream_id=${course_list}&reading=${
      values?.reading
    }&writing=${values?.writing}&listening=${values?.listening}&speaking=${
      values?.speaking
    }`;
    console.log(queryURL);

    axios
      .get(queryURL)
      .then((res) => {
        let { result, status } = res.data;
        console.log(result);
        setResultedData(result);
        setheading(true);
      })
      .catch((error) => console.log(error));
  };

  ///test area

  //test area
  const [country, setCountry] = useState([]);

  //use effect
  useEffect(() => {
    axios
      .get(`${window.APIPATH}/v1/countries`)
      .then((result) => {
        if (result.data.status) {
          setCountry(result.data.data);
        }
      })
      .catch((err) => {
        console.log(err);
      });

    axios
      .get(`${window.APIPATH}/v1/streams`)
      .then((result) => {
        if (result.data.status) {
          setstream(result.data.data);
        }
      })
      .catch((error) => console.log(error));
  }, []);

  return (
    <>
      <Navbar />
      <section class="eligibleSteps">
        <div class="container">
          <div class="row highlight-txt text-lett">
            <div class="col-md-12">
              {heading ? (
                <h3 class="mainheading">
                  You have{" "}
                  {resultedData &&
                    resultedData
                      .map((a) => {
                        return a.program.length;
                      })
                      .reduce((total, value) => {
                        return total + value;
                      })}{" "}
                  matching programs from {resultedData.length}{" "}
                  Schools/University!
                </h3>
              ) : (
                <h3 class="mainheading">
                  Get a list of eligible programs ... in just 60 seconds
                </h3>
              )}
            </div>
          </div>
          {!heading ? (
            <Main
              oneFunc={oneFunc}
              countries={country}
              highEducation={highEducation}
              streams={stream}
            />
          ) : (
            <div>
              {resultedData.map((school) => (
                <div key={school._id}>
                  <div className="card">
                    <div className="card-body">
                      <div className="container full-container">
                        <div className="row english-wrapper">
                          <div className="col-sm-1">
                            <div className="logo-wrapper">
                              <img src="image/one.jpg" className="img-fluid" />
                            </div>
                          </div>

                          <div className="col-sm-4">
                            <div className="school-wrapper">
                              <h5>{school.school_name}</h5>

                              <ul className="flag">
                                <li>
                                  <img
                                    src="image/flag.jpg"
                                    className="img-fluid"
                                  />
                                </li>
                                <li>{school.country}</li>
                              </ul>
                            </div>
                          </div>

                          <div className="col-sm-7">
                            <Button
                              variant="primary"
                              onClick={() => handleShow(school.id)}
                            >
                              view Eligible {school.program.length} programs
                            </Button>
                          </div>
                        </div>

                        <hr className="hr" />
                        <div className="row type-wrapper">
                          <div className="col-sm-4">
                            <div className="media students-wrapper">
                              <div className="local-wrapper">
                                <img
                                  src="image/users.png"
                                  className="mr-3"
                                  alt="..."
                                />
                              </div>
                              <div className="media-body">
                                <h5 className="mt-0 total-wrapper">
                                  Total Students
                                </h5>
                                <p>
                                  {school.aboutschool !== undefined
                                    ? school.aboutschool.total_student
                                    : ""}
                                </p>
                              </div>
                            </div>
                          </div>

                          <div className="col-sm-4">
                            <div className="media students-wrapper">
                              <div className="local-wrapper">
                                <img
                                  src="image/university.png"
                                  className="mr-3"
                                  alt="..."
                                />
                              </div>

                              <div className="media-body">
                                <h5 className="mt-0 total-wrapper">Type</h5>
                                <p>college school</p>
                              </div>
                            </div>
                          </div>

                          <div className="col-sm-4">
                            <div className="media">
                              <div className="local-wrapper">
                                <img
                                  src="image/search.png"
                                  className="mr-3"
                                  alt="..."
                                />
                              </div>
                              <div className="media-body">
                                <h5 className="mt-0 total-wrapper">Founded</h5>
                                <p>{school.school_logo.founded}</p>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
          <div>
            <Modal show={show} onHide={() => setShow(false)}>
              <Modal.Header closeButton>
                {/* <Modal.Title>Login Form</Modal.Title> */}
              </Modal.Header>
              <Modal.Body>
                {resultedPrograms.map((program) => (
                  <div key={program.id}>
                    <h3>{program.course}</h3>
                  </div>
                ))}
              </Modal.Body>
              <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                  Close Modal
                </Button>
              </Modal.Footer>
            </Modal>
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
}
