const {
  AGENT_REGISTER_REQUEST,
  AGENT_REGISTER_SUCCESS,
  AGENT_REGISTER_FAIL,
  AGENT_SIGNIN_REQUEST,
  AGENT_SIGNIN_SUCCESS,
  AGENT_SIGNIN_FAIL,
} = require("../constansts/agentConstants");

const agentRegisterReducer = (state = {}, action) => {
  switch (action.type) {
    case AGENT_REGISTER_REQUEST:
      return { loading: true };
    case AGENT_REGISTER_SUCCESS:
      return { loading: false, agentInfo: action.payload };
    case AGENT_REGISTER_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};

const agentLoginReducer = (state = {}, action) => {
  switch (action.type) {
    case AGENT_SIGNIN_REQUEST:
      return { loading: true };
    case AGENT_SIGNIN_SUCCESS:
      return { loading: false, agentInfo: action.payload };
    case AGENT_SIGNIN_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};

export { agentRegisterReducer, agentLoginReducer };
