import React from "react";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";

const phoneRegex = RegExp(
  /^(\+?\d{0,4})?\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{4}\)?)?$/
);

const schema = yup.object().shape({
  first_name: yup.string().required("Required."),
  last_name: yup.string().required("Required."),
  email: yup.string().email("Invalid Email").required("Required."),
  mobile: yup
    .string()
    .matches(phoneRegex, "Invalid number.")
    .required("Required."),
  term: yup.boolean().oneOf([true], "You must accept the terms and conditions"),
});

export default function SideBarForm() {
  const { register, handleSubmit, errors } = useForm({
    resolver: yupResolver(schema),
  }); // initialize the hook
  const onSubmit = (data) => {
    console.log(data);
    //handle submit here for something
  };

  return (
    <div className="sidebarform">
      <h3>Interested in studying abroad?</h3>
      <p className="sidebarformsubhead">
        Learn International can help  fill in your details and well call you
        back.
      </p>
      <form onSubmit={handleSubmit(onSubmit)} noValidate>
        <div className="form-group">
          <input
            name="first_name"
            type="text"
            className="form-control"
            placeholder="First Name*"
            ref={register}
          />
          <div>
            <p className="text-danger">{errors?.first_name?.message}</p>
          </div>
        </div>
        <div className="form-group">
          <input
            name="last_name"
            type="text"
            className="form-control"
            placeholder="Last Name*"
            ref={register}
          />
          <div>
            <p className="text-danger">{errors?.last_name?.message}</p>
          </div>
        </div>
        <div className="form-group">
          <input
            name="email"
            type="email"
            className="form-control"
            placeholder="Email*"
            ref={register}
          />
          <div>
            <p className="text-danger">{errors?.email?.message}</p>
          </div>
        </div>
        <div className="form-group">
          <input
            name="mobile"
            type="text"
            className="form-control"
            placeholder="Mobile*"
            ref={register}
          />
          <div>
            <p className="text-danger">{errors?.mobile?.message}</p>
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="destination1">Your preferred study destination</label>
          <select
            className="form-control"
            id="destination1"
            name="destination"
            ref={register}
          >
            <option value>--Select--</option>
            <option>New Zealand</option>
            <option>Canada</option>
            <option>United Kingdom</option>
            <option>Australia</option>
            <option>USA</option>
            <option>Cyprus</option>
            <option>Germany</option>
            <option>Singapore</option>
            <option>Bulgaria</option>
            <option>czech republic</option>
            <option>Dubai</option>
            <option>Hungary</option>
            <option>Ireland</option>
            <option>Latvia</option>
            <option>Malta</option>
            <option>Netherlands</option>
            <option>Poland</option>
            <option>Russia</option>
            <option>Slovenia</option>
            <option>South Korea</option>
            <option>Spain</option>
            <option>Sweden</option>
          </select>
        </div>
        <p className="linote">
          Learn International nde omnis iste natus error sit voluptatem
          accusantium doloremque laudantium
        </p>
        <div className="form-check">
          <div>
            <p className="text-danger">{errors?.term?.message}</p>
          </div>
          <input
            name="term"
            className="form-check-input"
            type="checkbox"
            defaultValue="true"
            id="defaultCheck1"
            ref={register}
          />
          <label className="form-check-label" htmlFor="defaultCheck1">
            <span>
              I agree to Learn International <a href="#">Terms</a> and
              <a href="#">Privacy Policy</a>
            </span>
          </label>
        </div>

        <button type="submit" className="register">
          Register Now
        </button>
      </form>
    </div>
  );
}
